@import './variables.scss';

@mixin flexCenter($direction) {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    padding: 0 3rem;
    flex-direction: $direction;
}

@mixin flexLeft {
    display: flex;
    align-items: flex-start;
    padding-left: 9rem;
}

@mixin heading {
    color: $headingcolor;
    font-size: 2rem;
    margin-top: 2rem;
    font-family: $headingfont;
    font-weight: 400;
    font-size: 2.8rem;
    padding-bottom: 4rem;
}

@mixin para-text {
    text-align: left;
    font-size: 1.2rem;
    font-family: $textfont;
    font-weight: 400;
    width: 45%;
    line-height: 1.5rem;
}

@mixin svg-img {
    width: 20rem;
}

//mobile
@mixin mobile-flexCenter {
    padding: 0 2rem;
}

@mixin mobile-para-text {
    font-size: 1rem;
    line-height: 1.3;
    width: 100%;
    padding: 0 0 1rem 0;
}

@mixin mobile-heading {
    font-size: 1.8rem;
    margin-top: 1.5rem;
    padding-bottom: 2rem;
}

//small mobile
@mixin mobile-svg {
    padding-bottom: 2rem;
}

@mixin mobile-svg-small {
    width: 15rem;

}

//contact us

@mixin inputbox($height) {
    font-family: $textfont;
    font-size: 1rem;
    background-color: #ffffff;
    text-align: left;
    border-radius: 5px;
    border: none;
    padding: 0.5rem;
    width: 90%;
    height: $height;
    text-align: start;
    margin: 0.2rem 0;
}